import React from "react";
import {useHistory} from 'react-router-dom';
import {BaseTheme} from "../../../Themes/DefaultTheme";
import {ThemeProvider} from '@material-ui/core/styles';
import useStyles from "./style";

//MUI components
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import Typography from "@material-ui/core/Typography";


export default function Footer() {
    const history = useHistory();
    const classes = useStyles();

    function changeRoute(route) {
        history.push(route);
    }

    return (
        <Grid item xs={12}>
            <ThemeProvider theme={BaseTheme}>
                <Box p={2} className={classes.footer}>
                    <Grid container>
                        <Grid item xs={12} md={3}>
                            <List>
                                <ListItem>
                                    
                                </ListItem>
                                <ListItem>
                                    <Link href={'#'} onClick={event => changeRoute(`/user/13`)}>
                                         
                                    </Link>
                                </ListItem>
                                <ListItem>
                                    <Link href={'#'} onClick={event => changeRoute(`/user/19`)}>
                                         
                                    </Link>
                                </ListItem>
                                <ListItem>
                                    <Link href={'#'} onClick={event => changeRoute(`/user/25`)}>
                                         
                                    </Link>
                                </ListItem>
                            </List>
                        </Grid>
                        <Grid item xs={12} md={3}>
                            <List>
                                <ListItem>
                                    
                                </ListItem>
                                <ListItem>
                                    <Link href={'#'} onClick={event => changeRoute(`/threads`)}>
                                        
                                    </Link>
                                </ListItem>
                                <ListItem>
                                    <Link href={'#'} onClick={event => changeRoute(`/users`)}>
                                        
                                    </Link>
                                </ListItem>
                                <ListItem>
                                    <Link href={'#'} onClick={event => changeRoute(`/editthread/new`)}>
                                         
                                    </Link>
                                </ListItem>
                            </List>
                        </Grid>
                        <Grid item xs={12} md={3}>
                            <List>
                                <ListItem>
                                     
                                </ListItem>
                                <ListItem>
                                    <Link href={'https://reactjs.org/'}>
                                        React
                                    </Link>
                                </ListItem>
                                <ListItem>
                                    <Link href={'https://material-ui.com/'}>
                                        Material UI
                                    </Link>
                                </ListItem>
                                <ListItem>
                                    <Link href={'https://laravel.com/'}>
                                        Laravel
                                    </Link>
                                </ListItem>
                            </List>
                        </Grid>
                        <Grid item xs={12} md={3}>
                            <List>
                                <ListItem>
                                    
                                </ListItem>
                                <ListItem>
                                    <Link href={'https://github.com/DanilAndreev/starscream_project'}>
                                        Github 
                                    </Link>
                                </ListItem>
                                <ListItem>
                                    <Link href={'https://github.com/DanilAndreev/starscream_project/tree/master/web'}>
                                        Github  front-end
                                    </Link>
                                </ListItem>
                                <ListItem>
                                    <Link
                                        href={'https://github.com/DanilAndreev/starscream_project/tree/master/backend'}>
                                        Github  back-end
                                    </Link>
                                </ListItem>
                            </List>
                        </Grid>
                    </Grid>
                </Box>
                <Grid container className={classes.copyright}>
                    <Box p={2}>
                        <Grid item xs={12}>
                            <Typography variant={'subtitle2'} align={'center'}>
                                Copyright () Andrieiev Solomaha Nesterov 2020 All rights reserved
                            </Typography>
                        </Grid>
                    </Box>
                </Grid>
            </ThemeProvider>
        </Grid>
    );
}